<template>
  <v-navigation-drawer
    v-model="drawer"
    fixed
    app
    width="250"
  >
    <v-layout column align-center>
      <v-flex>
        <img class="logo_img" :src="require('@/assets/logo.png')"/>
      </v-flex>
    </v-layout>
    <v-list dense class="menu">
      <v-list-group
        v-for="item in items"
        :key="item.title"
        v-model="item.active"
        no-action
        class="drawer"
      >
        <template v-slot:activator>
          <v-list-tile>
            <v-list-tile-content>
              <v-list-tile-title>{{ item.title }}</v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </template>

        <v-list-tile
          v-for="subItem in item.subItems"
          :key="subItem.title"
          :to="subItem.to"
        >
          <v-list-tile-action>
            <v-icon>{{ subItem.icon }}</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>{{ subItem.title }}</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list-group>
    </v-list>
  </v-navigation-drawer>
</template>

<script>

import {mapGetters} from 'vuex';

export default {
  data: () => ({
    drawer: true,
    items: [
      {
        title: '1_ Leaflet',
        active: true,
        subItems: [
          { title: 'Leaflet_GeoJson' ,  to: '/Leaflet' ,          icon: 'map'},
          { title: 'basic' ,            to: '/Leaflet_basic' ,    icon: 'add_box' },
          { title: 'polygon' ,          to: '/Leaflet_polygon' ,  icon: 'add_box' },
          { title: 'click' ,            to: '/Leaflet_click' ,    icon: 'add_box' },
          { title: 'geofence' ,         to: '/Leaflet_geofence' , icon: 'add_box' }
        ]
      },
      {
        title: '2_ VueLayer',
        subItems: [
          { title: 'Vuelayer_basic' ,   to: '/Vuelayer_basic' ,     icon: 'map'},
          { title: 'VueLayer_geofence' ,to: '/VueLayer_geofence' ,  icon: 'add_box' },
          { title: '3_' ,               to: '/UserGroup' ,          icon: 'add_box' }
        ]
      },
      {
        title: '3_ html',
        subItems: [
          { title: '1_iframe' , to: '/iframe' , icon: 'desktop_mac'},
          { title: '2_fileRead' , to: '/fileRead' , icon: 'add_box'},
        ]
      },
      {
        title: '4_',
        subItems: [
          { title: '1_' , to: '/Language' , icon: 'language'},
          { title: '2_' , to: '/Message' , icon: 'message' },
          { title: '3_' , to: '/Code' , icon: 'code' }
        ]
      }
    ]
  }),
  computed: {
  },
  methods:{
    drawerToggle(){
      this.drawer = !this.drawer;
    }
  },
  created() {
    this.$eventBus.$on('drawer', () =>{ this.drawerToggle() });
  }
}
</script>

<style>
.logo_img{
  width: 100%;
  padding: 4rem;
}
.menu{
  width: 100%;
}
.v-list__tile {
  padding: 0 25px !important;
}
.v-list__tile__title {
  font-weight: 550 !important;
}
.theme--light.v-list{
  color: #8f2c35;
}
.theme--light.v-icon {
  color: #8c8c8c;
}
.drawer .primary--text {
  color: #3692EE !important;
  background: #ededed !important;
  border-right: 3px solid #3692EE !important;
}
.v-list__group__items--no-action .v-list__tile {
  padding-left: 40px !important;
}
</style>
