import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);

export default new Router({
  base: __dirname,
  mode: 'history',
  // 페이지 고침시 사용자 화면을 0,0으로 위치 이동
  scrollBehavior: function(to) {
	if (to.history) {
		return { selector: to.history }
			} else {
			return { x: 0, y: 0 }
		}
	},
  routes: [
    {
      path: '/',
      name: '01_Leaflet',
      component: ()=> import('@/page/1_/01'),
      children:[
        {
          path: '/Leaflet',
          name: 'Leaflet',
          component: ()=> import('@/page/1_/Leaflet')
        },
        {
          path: '/Leaflet_basic',
          name: 'Leaflet_basic',
          component: ()=> import('@/page/1_/Leaflet_basic')
        },
        {
          path: '/Leaflet_polygon',
          name: 'Leaflet_basic',
          component: ()=> import('@/page/1_/Leaflet_polygon')
        },
        {
          path: '/Leaflet_click',
          name: 'Leaflet_click',
          component: ()=> import('@/page/1_/Leaflet_click')
        },
        {
          path: '/Leaflet_geofence',
          name: 'Leaflet_geofence',
          component: ()=> import('@/page/1_/Leaflet_geofence')
        }
      ]
    },
    {
      path: '/02',
      name: '02_VueLayer',
      component: ()=> import('@/page/2_/02'),
      children:[
        {
          path: '/VueLayer_basic',
          name: 'VueLayer_basic',
          component: ()=> import('@/page/2_/VueLayer_basic')
        },
        {
          path: '/VueLayer_geofence',
          name: 'VueLayer_geofence',
          component: ()=> import('@/page/2_/VueLayer_geofence')
        },
        {
          path: '/UserGroup',
          name: 'UserGroup',
          component: ()=> import('@/page/2_/UserGroup')
        },
        {
          path: '/Component',
          name: 'Component',
          component: ()=> import('@/page/2_/Component')
        }
      ]
    },
    {
      path: '/03',
      name: '03',
      component: ()=> import('@/page/3_/03'),
      children:[
        {
          path: '/iframe',
          name: 'iframe',
          component: ()=> import('@/page/3_/iframe')
        },
        {
          path: '/fileRead',
          name: 'fileRead',
          component: ()=> import('@/page/3_/fileRead')
        }
      ]
    },
    {
      path: '/04',
      name: '04',
      component: ()=> import('@/page/4_/04'),
      children:[
        {
          path: '/Language',
          name: 'Language',
          component: ()=> import('@/page/4_/Language')
        },
        {
          path: '/Message',
          name: 'Message',
          component: ()=> import('@/page/4_/Message')
        },
        {
          path: '/Code',
          name: 'Code',
          component: ()=> import('@/page/4_/Code')
        }
      ]
    }
  ]
});